@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
.header{height:100vh;width:100%;font-size:1.5em;position:relative}.header-bg{position:absolute;top:0;bottom:0;right:0;left:0;width:100%;height:100%;background-image:-webkit-gradient(linear, left top, left bottom, from(#ff9d2f), to(#fbd784));background-image:linear-gradient(#ff9d2f, #fbd784);-webkit-transform:skewY(-8deg);transform:skewY(-8deg);-webkit-transform-origin:top left;transform-origin:top left}.header-title{margin:0 auto;font-size:2.5em;color:#fff;font-weight:700;line-height:56px;z-index:999}.header-title-name{margin:0 5px;font-size:18px;line-height:24px;font-weight:800;letter-spacing:6px;text-transform:uppercase;color:#0B1D26}.header-title .subtitle{margin-top:1rem;font-size:0.5em;overflow:hidden;border-right:.1em solid #fff;white-space:nowrap;letter-spacing:.15em;-webkit-animation:typing 5.5s steps(26, start),caret 0.7s step-end infinite;animation:typing 5.5s steps(26, start),caret 0.7s step-end infinite}.header-title .social-links{font-size:2rem;margin-top:15px}.header-title .social-links .icon{color:#0B1D26;margin-right:20px;-webkit-transition:0.3s;transition:0.3s}.header-title .social-links .icon:hover{color:#fff}@-webkit-keyframes typing{from{width:0}to{width:100%}}@keyframes typing{from{width:0}to{width:100%}}@-webkit-keyframes caret{from,to{border-color:transparent}50%{border-color:#fff}}@keyframes caret{from,to{border-color:transparent}50%{border-color:#fff}}@media (max-width: 767px){.header-title{text-align:center;left:0;font-size:1.3em;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}}

.nav-menu{justify-self:flex-start;position:fixed;z-index:9999;margin-left:20px}.nav-menu .navbar-links .icon{font-size:22px}.nav-menu .navbar-links .link{display:flex;align-items:center;padding:10px 18px;margin-bottom:15px;border-radius:50px;height:56px;width:56px;overflow:hidden;-webkit-transition:0.4s;transition:0.4s;color:#fff;text-transform:uppercase;font-size:20px;-webkit-transition:linear 0.4s;transition:linear 0.4s}.nav-menu .navbar-links .link:hover{text-decoration:none;color:#f2f3f5}.nav-menu .navbar-links .link:hover{width:100%}.nav-menu .navbar-links .link:hover span{display:inline-block;margin-left:15px}.nav-menu .navbar-links .link span{display:none}.nav-menu .navbar-mobile{display:none}@media (max-width: 768px){.nav-menu{position:absolute;top:0;right:0;width:100vw;background-color:#0B1D26}.nav-menu .navbar-desktop{display:none}.nav-menu .navbar-mobile{top:0;right:0;display:block;z-index:9998;font-size:1.5rem;-webkit-transition:all 0.4s;transition:all 0.4s;outline:none !important;cursor:pointer;overflow:hidden}.nav-menu .navbar-mobile .navbar-nav{background-color:#0B1D26;height:100vh;display:flex;justify-content:center}.nav-menu .navbar-mobile .navbar-nav .nav-link{color:#fff}}

.section-title{display:flex;margin-bottom:32px;align-items:center;color:#fbd784}.section-title-line{width:72px;height:2px;margin-right:24px;background-color:#fbd784}.section-title-name{margin-top:0px;margin-bottom:0px;font-size:14px;line-height:24px;font-weight:800;letter-spacing:6px;text-transform:uppercase}@media (max-width: 768px){.section-title{width:100%}}

.intro{padding:40px}.intro img{width:16px;height:24px;fill:#fbd784}@media (max-width: 768px){.intro{font-size:1em;padding:0}}

.skills-stack{padding:20px 0;margin:0 20px;max-width:900px}.skills-stack .skills{width:100px}.skills-stack .icons{font-size:64px;padding:2px;color:#fbd784}

.small-title{margin-top:0px;margin-bottom:24px;font-family:'Serif Display', sans-serif;line-height:110%;color:#fff}

.content{max-width:1440px}.grid{margin-bottom:10em}.grid .content-text{position:relative;width:50%;max-width:550px;text-align:left}.grid .content-text .count{position:absolute;left:-110px;top:-80px;opacity:0.1;font-size:200px;line-height:100%;font-weight:700;color:#fff;padding:0.25rem}.grid .content-text p{margin-bottom:32px;color:#fff}.grid .content-text .link{color:#fbd784;font-weight:700}.grid .image{width:480px;height:580px;margin-top:3rem}.grid .main-title{color:#2d2d2d;text-align:center;text-transform:capitalize;padding:0.7em 0}.grid .content{position:relative;margin:auto;overflow:hidden;cursor:pointer}.grid .content:hover .content-overlay{opacity:0}.grid .content:hover .content-details{top:50%;left:-30%;opacity:1}.grid .content .content-overlay{background:#fbd784;position:absolute;left:0;top:0;bottom:0;right:0;opacity:1;margin-top:3rem;-webkit-transition:all 0.4s ease-in-out 0s;transition:all 0.4s ease-in-out 0s}.grid .content .content-details{position:absolute;top:50%;left:50%;opacity:1;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);-webkit-transition:all 0.3s ease-in-out 0s;transition:all 0.3s ease-in-out 0s}.grid .content .content-details h1{font-size:10em}@media (max-width: 768px){.grid .image{height:280px}}

#contact{padding:0 20px 30px;margin-bottom:0;position:relative}#contact .footer{justify-content:space-between}#contact .icons{display:flex;align-items:center;padding:10px 18px;margin-bottom:15px;border-radius:50px;height:56px;width:56px;overflow:hidden;-webkit-transition:0.4s;transition:0.4s;background-color:rgba(255,255,255,0.1);color:#fbd784;overflow:visible}#contact .icons:hover{text-decoration:none;color:#f2f3f5}#contact .back-to-top{position:absolute;bottom:30px;right:10px;background-color:rgba(255,255,255,0.1);color:#fbd784;padding:10px 18px;margin-bottom:8px;border-radius:50px;border:none;height:56px;width:56px}@media (max-width: 768px){.footer-icons{width:100%}}

*{margin:0;padding:0;box-sizing:border-box}h5{font-weight:700;color:#fbd784}.home{background-color:#0B1D26;overflow:hidden}.section{padding:30px 0;font-family:'Montserrat', sans-serif;width:100%;color:#fff;margin-bottom:40px}.content-wrapper{display:flex;justify-content:space-between;flex-wrap:wrap;align-items:center;margin:0 auto;padding:0 70px;margin-top:5rem}@media (max-width: 768px){.content-wrapper{justify-content:center}}

